*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: "Open Sans", sans-serif;
  background-color: $background;
  color: $primary;
  height: 100vh;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}
